<template>
  <div>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/' }"> </el-breadcrumb-item>
      <el-breadcrumb-item
        v-for="item in levelList"
        :key="item.name"
        :to="{ path: item.link }"
        >{{ item.title }}
      </el-breadcrumb-item>
    </el-breadcrumb>
  </div>
</template>

<script>
const config = [
  { name: 'ArticleList', title: '', link: '/article' },
  {
    name: 'ArticleNew',
    title: '',
    parent: 'ArticleList',
  },
  {
    name: 'ArticleEdit',
    title: '',
    parent: 'ArticleList',
  },
  { name: 'CommentList', title: '' },
  { name: 'MetaList', title: '/' },
  { name: 'MediaList', title: '' },
  { name: 'Setting', title: '' },
]

export default {
  name: 'BreadCrumb',
  data() {
    return {
      levelList: [],
    }
  },
  methods: {
    initLevelList() {
      this.levelList = []
      if (!this.$route.name) {
        return
      }
      this.getBreadcrumb(this.$route.name)
      this.levelList.reverse()
    },
    getBreadcrumb(name) {
      let breadCrumbItem = null
      config.forEach((c) => {
        if (c.name === name) {
          breadCrumbItem = c
        }
      })
      if (null === breadCrumbItem) {
        return
      }
      this.levelList.push(breadCrumbItem)
      if (breadCrumbItem.parent && breadCrumbItem.parent !== '') {
        this.getBreadcrumb(breadCrumbItem.parent)
      }
    },
  },
  watch: {
    $route(route) {
      // if you go to the redirect page, do not update the breadcrumbs
      if (route.path.startsWith('/redirect/')) {
        return
      }
      this.initLevelList()
    },
  },
  created() {
    this.initLevelList()
  },
}
</script>

<style scoped></style>
