<template>
  <div>
    <el-tabs>
      <el-tab-pane label="">
        <el-form ref="websiteForm" :model="options" :rules="websiteRules">
          <el-form-item label=":">
            <el-input v-model="options.blog_name" placeholder="" />
          </el-form-item>
          <el-form-item label=":" prop="blog_website">
            <el-input
              v-model="options.blog_website"
              placeholder="http, https:// "
            />
          </el-form-item>
          <el-form-item label=":">
            <el-input
              type="textarea"
              :rows="6"
              v-model="options.blog_footer"
              placeholder=",html"
            />
          </el-form-item>
          <el-form-item label=":" prop="summary_flag">
            <el-input
              v-model="options.summary_flag"
              placeholder=",255"
            />
          </el-form-item>
          <el-form-item>
            <el-button
              type="primary"
              size="small"
              @click="submitAfterValidate('websiteForm')"
              >
            </el-button>
          </el-form-item>
        </el-form>
      </el-tab-pane>
      <el-tab-pane label="SEO ">
        <el-form ref="form" :model="options">
          <el-form-item label="(Title):">
            <el-input
              v-model="options.meta_title"
              placeholder=""
            />
          </el-form-item>
          <el-form-item label="(description):">
            <el-input
              v-model="options.meta_description"
              placeholder=""
            />
          </el-form-item>
          <el-form-item label="(keywords):">
            <el-input
              v-model="options.meta_keywords"
              placeholder=""
            />
          </el-form-item>

          <el-form-item label="Google(google-site-verification):">
            <el-input
              v-model="options.google_site_verification"
              placeholder="Google"
            />
          </el-form-item>

          <el-form-item label="Baidu(baidu-site-verification):">
            <el-input
              v-model="options.baidu_site_verification"
              placeholder="Baidu"
            />
          </el-form-item>

          <el-form-item label="Google(google_analytics):">
            <el-input
              v-model="options.google_analytics"
              placeholder="UA-XXXXXXXX-X"
            />
          </el-form-item>

          <el-form-item>
            <el-button type="primary" size="small" @click="submitOptions"
              >
            </el-button>
          </el-form-item>
        </el-form>
      </el-tab-pane>
      <el-tab-pane label="">
        <el-form ref="emailForm" :model="options" :rules="emailRules">
          <el-form-item label=":">
            <el-switch v-model="options.is_email"></el-switch>
          </el-form-item>
          <div v-show="options.is_email">
            <el-form-item label=":" prop="email_username">
              <el-input
                v-model="options.email_username"
                placeholder=""
              />
            </el-form-item>
            <el-form-item label=":" prop="email_password">
              <el-input
                v-model="options.email_password"
                placeholder=""
              />
            </el-form-item>
            <el-form-item label=":" prop="email_host">
              <el-input
                v-model="options.email_host"
                placeholder="smtp.163.com"
              />
            </el-form-item>
            <el-form-item label=":" prop="email_port">
              <el-input
                v-model="options.email_port"
                placeholder=""
              />
            </el-form-item>
            <el-form-item label=":" prop="email_subject">
              <el-input
                v-model="options.email_subject"
                placeholder=""
              />
            </el-form-item>
          </div>
          <el-form-item>
            <el-button
              type="primary"
              size="small"
              @click="submitAfterValidate('emailForm')"
              >
            </el-button>
          </el-form-item>
        </el-form>
      </el-tab-pane>
      <el-tab-pane label="">
        <el-form ref="userForm" :model="userForm" :rules="userRules">
          <p class="tip"></p>
          <el-form-item label=":" prop="username">
            <el-input v-model="userForm.username" placeholder="" />
          </el-form-item>
          <el-form-item label=":" prop="email">
            <el-input v-model="userForm.email" placeholder="" />
          </el-form-item>
          <el-form-item>
            <el-button
              type="primary"
              size="small"
              @click="submitAfterValidate('userForm')"
              >
            </el-button>
          </el-form-item>
        </el-form>
      </el-tab-pane>
      <el-tab-pane label="">
        <el-form
          ref="passwordForm"
          :model="passwordForm"
          :rules="passwordRules"
        >
          <p class="tip"></p>
          <el-form-item label=":" prop="oldPassword">
            <el-input
              type="password"
              v-model="passwordForm.oldPassword"
              placeholder=""
            />
          </el-form-item>
          <el-form-item label=":" prop="newPassword">
            <el-input
              type="password"
              v-model="passwordForm.newPassword"
              placeholder=""
            />
          </el-form-item>
          <el-form-item label=":" prop="repeatPassword" required>
            <el-input
              type="password"
              v-model="passwordForm.repeatPassword"
              placeholder=""
            />
          </el-form-item>
          <el-form-item>
            <el-button
              type="primary"
              size="small"
              @click="submitAfterValidate('passwordForm')"
              >
            </el-button>
          </el-form-item>
        </el-form>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
    data: function () {
        const repeatPasswordValidate = (rule, value, callback) => {
            if (value === '') {
                callback(new Error(''));
            } else if (value !== this.passwordForm.newPassword) {
                callback(new Error(''))
            } else {
                callback()
            }
        }
        const emailSettingValidate = (rule, value, callback) => {
            if (!this.options.is_email) {
                return callback()
            }
            if (value === '') {
                callback(new Error(''))
            }
        }
        return {
            userForm: {
                username: '',
                email: ''
            },
            passwordForm: {
                oldPassword: '',
                newPassword: '',
                repeatPassword: ''
            },
            options: {
                blog_name: '',
                blog_website: '',
                blog_footer: '',
                summary_flag: '',
                meta_title: '',
                meta_description: '',
                meta_keywords: '',
                google_site_verification: '',
                baidu_site_verification: '',
                google_analytics: '',
                is_email: false,
                email_username: '',
                email_password: '',
                email_host: '',
                email_port: '',
                email_subject: ''
            },
            websiteRules: {
                blog_website: [{type: 'url', message: '', trigger: 'blur'}]
            },
            emailRules: {
                email_username: [
                    {type: 'email', message: '', trigger: 'blur'},
                    {validator: emailSettingValidate, trigger: 'blur'}
                ],
                email_password: [{validator: emailSettingValidate, trigger: 'blur'}],
                email_host: [{validator: emailSettingValidate, trigger: 'blur'}],
                email_port: [
                    {validator: emailSettingValidate, trigger: 'blur'}
                ]
            },
            userRules: {
                username: [{required: true, message: '', trigger: 'blur'}],
                email: [
                    {type: 'email', message: '', trigger: 'blur'},
                    {required: true, message: '', trigger: 'blur'}
                ]
            },
            passwordRules: {
                oldPassword: [
                    {required: true, message: '', trigger: 'blur'}
                ],
                newPassword: [
                    {required: true, message: '', trigger: 'blur'}
                ],
                repeatPassword: [{validator: repeatPasswordValidate, trigger: 'blur'}
                ]
            }
        }
    },
    methods: {
        getUser() {
            this.$api.auth.getUser().then(data => {
                this.userForm.username = data.data.username
                this.userForm.email = data.data.email
            })
        },
        getOptions() {
            this.$api.auth.getOptions().then(data => {
                if (data.success) {
                    const options = data.data
                    for (let key in options) {
                        this.options[key] = options[key]
                    }
                    this.options.is_email = this.options.is_email && this.options.is_email === 'true'
                } else {
                    this.$util.message.error(':' + data.msg)
                }
            })
        },
        submitUser() {
            this.$api.auth.resetUser(this.userForm.username, this.userForm.email).then(data => {
                if (data.data === true) {
                    this.$util.message.success('!')
                } else {
                    const message = data.msg || ','
                    this.$util.message.error(message)
                }
                this.$router.push('/admin/login')
            })
        },
        submitPassword() {
            this.$api.auth.resetPassword(this.passwordForm.oldPassword, this.passwordForm.newPassword).then(data => {
                if (data.data === true) {
                    this.$util.message.success('!')
                } else {
                    const message = data.msg || ','
                    this.$util.message.error(message)
                }
                this.$router.push('/admin/login')
            })
        },
        submitOptions() {
            this.$api.auth.saveOptions(this.options)
                .then(() => {
                    this.$util.message.success('!')
                })
        },
        submitAfterValidate(formName) {
            let fuc
            if (formName === 'userForm') {
                fuc = this.submitUser
            } else if (formName === 'passwordForm') {
                fuc = this.submitPassword
            } else {
                fuc = this.submitOptions
            }
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    fuc()
                }
            })
        }
    }
    ,
    mounted() {
        this.getUser()
        this.getOptions()
    }
}
</script>

<style scoped>
.tip {
  font-size: 14px;
  color: #5e6d82;
  line-height: 1.5em;
}
</style>
