<template>
  <el-upload
    ref="importUpload"
    class="inline-upload"
    accept="md"
    action="#"
    :show-file-list="false"
    :auto-upload="false"
    :on-change="onFileChange"
  >
    <el-button size="small" type="danger"></el-button>
  </el-upload>
</template>

<script>
export default {
  name: 'ArticleUpload',
  props: {
    articleId: {
      type: Number,
      default: null,
    },
    articleTitle: {
      type: String,
      default: '',
    },
    afterImport: {
      type: Function,
      default: function () {},
    },
  },
  data: function () {
    return {}
  },
  methods: {
    onFileChange(file) {
      let _this = this
      // 
      const msgData = []
      const h = this.$createElement
      msgData.push(h('p', null, '[' + file.name + ']'))
      msgData.push(h('p', null, ''))
      msgData.push(h('p', null, '[' + this.articleTitle + ']'))
      msgData.push(h('p', null, '?'))

      this.$confirm('', {
        message: h('div', null, msgData),
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning',
      })
        .then(() => {
          if (!_this.beforeImport(file)) {
            return
          }

          const articleId = _this.articleId
          _this.$api.auth
            .importArticle(file.raw, articleId)
            .then((data) => {
              _this.successImport(data, file)
            })
            .catch((err) => {
              _this.errorImport(err, file)
            })
        })
        .catch(() => {})
    },
    beforeImport(file) {
      let fileName = file.name

      const size = file.size / (1024 * 1024)
      if (size > 10) {
        this.$util.message.error(fileName + '10m')
        return false
      }
      return true
    },
    successImport(response, file) {
      if (response.success) {
        this.$util.message.success('' + file.name + '!')
      } else {
        this.$util.message.error('' + file.name + '!' + response.msg)
      }
      this.afterImport()
    },
    errorImport(err, file) {
      this.$util.message.error('' + file.name + '!')
      console.log(err)
    },
  },
}
</script>

<style scoped>
.inline-upload {
  display: inline;
  margin-right: 10px;
}
</style>
