import axios from 'axios'
import qs from 'qs'
import { Message, Loading } from 'element-ui'
import router from '../router/index'
import serverConfig from '../../server-config'

const axiosJson = axios.create({
  baseURL: serverConfig.api + 'api/', // 
  timeout: 5000,
  // params
  paramsSerializer: (params) => {
    // tomcat8.5
    return qs.stringify(params, { indices: false })
  },
})

let loadingInstance = null
let loginError = false

// 
const requestInterceptor = {
  before: (config) => {
    if (loadingInstance === null) {
      loadingInstance = Loading.service({ target: '#main', fullscreen: false })
    }

    // 
    if (localStorage.token) {
      config.headers.Authorization = 'Bearer ' + localStorage.token
    }

    return config
  },
  error: (error) => {
    Message({
      showClose: true,
      message: error,
      type: 'error.data.error.message',
    })
    return Promise.reject(error)
  },
}

// 
const responseInterceptor = {
  after: async (response) => {
    // 
    if (loadingInstance !== null) {
      loadingInstance.close()
      loadingInstance = null
    }

    if (
      response.headers['content-type'] &&
      response.headers['content-type'].indexOf('json') === -1
    ) {
      // json
      return response
    }

    if (response.data && !response.data.success) {
      return await handleServiceError(response)
    }
    return response
  },
  error: (error) => {
    // 
    if (loadingInstance !== null) {
      loadingInstance.close()
      loadingInstance = null
    }
    let msg = ''
    if (!error.response) {
      msg = error.message
    } else {
      router.push('/error/' + error.response.status + '/' + msg)
    }
    Message({
      showClose: true,
      message: msg,
      type: 'error',
    })

    return Promise.reject(error)
  },
}

// axiosJson
axiosJson.interceptors.request.use(
  requestInterceptor.before,
  requestInterceptor.error
)

axiosJson.interceptors.response.use(
  responseInterceptor.after,
  responseInterceptor.error
)

/**
 * 
 * @param {Object} response
 * @returns response
 */
async function handleServiceError(response) {
  switch (response.data.code) {
    case 999:
      return handleNotLogin(response)
    case 998:
      return handleLoginExpire(response)
    default:
      return handleDefatutError(response)
  }
  return response
}

/**
 * 
 * @param {Object} response
 * @returns response
 */
function handleNotLogin(response) {
  router.push(
    '/login',
    () => {},
    () => {}
  )
  if (!loginError) {
    loginError = true
    Message({
      showClose: true,
      message: ',',
      type: 'error',
      onClose: function () {
        loginError = false
      },
    })
  }
  return response
}

/**
 * 
 * @param {Object} response
 * @returns response
 */
async function handleLoginExpire(response) {
  const token = localStorage.getItem('token')
  const refreshToken = localStorage.getItem('refreshToken')
  console.log('refreshToken: ' + refreshToken)
  if (refreshToken) {
    // refresh_token token
    try {
      const res = await axios({
        method: 'POST',
        url: serverConfig.api + 'api/admin/refresh',
        headers: {
          Authorization: `Bearer ${token}`,
        },
        data: {
          refreshToken,
        },
      })
      //  token 
      console.log(' token  ', res)
      if (res.data && res.data.success) {
        localStorage.setItem('token', res.data.data.token)
        localStorage.setItem('refreshToken', res.data.data.refreshToken)
        // 
        return axiosJson(response.config)
      } else {
        router.push(
          '/login',
          () => {},
          () => {}
        )
        if (!loginError) {
          loginError = true
          Message({
            showClose: true,
            message: ',',
            type: 'error',
            onClose: function () {
              loginError = false
            },
          })
        }
      }
    } catch (err) {
      console.log(' token ', err)
    }
  }
  return response
}

/**
 * 
 * @param {Object} response
 * @returns response
 */
function handleDefatutError(response) {
  let msg = response.data.msg || ''
  console.error(
    'Axios response error.Url: ' +
      response.request.responseURL +
      ', code: ' +
      response.data.code +
      ', msg: ' +
      response.data.msg
  )
  Message({
    showClose: true,
    message: msg,
    type: 'error',
  })
  return response
}

/**
 * get 
 * @param url
 * @param params
 * @returns {Promise}
 */
export function get(url, params = {}) {
  return new Promise((resolve, reject) => {
    axiosJson
      .get(url, {
        params: params,
      })
      .then((response) => {
        resolve(response.data)
      })
      .catch((err) => {
        reject(err)
      })
  })
}

/**
 * post 
 * @param url
 * @param params
 * @returns {Promise}
 */
export function post(url, params = {}) {
  return new Promise((resolve, reject) => {
    axiosJson.post(url, params).then(
      (response) => {
        resolve(response.data)
      },
      (err) => {
        reject(err)
      }
    )
  })
}

/**
 * put
 * @param url
 * @param params
 * @returns {Promise}
 */
export function put(url, params = {}) {
  return new Promise((resolve, reject) => {
    axiosJson.put(url, params).then(
      (response) => {
        resolve(response.data)
      },
      (err) => {
        reject(err)
      }
    )
  })
}

/**
 * delete
 * @param url
 * @param params
 * @returns {Promise}
 */
export function del(url, params = {}) {
  return new Promise((resolve, reject) => {
    axiosJson
      .delete(url, {
        params: params,
      })
      .then((response) => {
        resolve(response.data)
      })
      .catch((err) => {
        reject(err)
      })
  })
}

export function download(url, params = {}) {
  return new Promise((resolve, reject) => {
    axiosJson.post(url, params, { responseType: 'blob' }).then(
      (response) => {
        resolve(response)
      },
      (err) => {
        reject(err)
      }
    )
  })
}
